.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Ensure 3D transforms work properly */
* {
  transform-style: preserve-3d;
}

/* Flip card specific styles */
.flip-card-container {
  transform-style: preserve-3d;
  perspective: 1000px;
}

.flip-card-front,
.flip-card-back {
  backface-visibility: hidden;
  transform-style: preserve-3d;
}

/* Flip card animation classes */
.flip-card {
  width: 200px;
  height: 280px;
  position: relative;
  perspective: 1000px;
  transform-style: preserve-3d;
  overflow: hidden;
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 8px 16px 32px 0 #0006;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.4, 0.0, 0.2, 1);
  overflow: hidden;
}

.flip-card-back {
  background: linear-gradient(135deg, #2a2a3a 0%, #1a1a2a 100%);
  border: 2px solid #444;
  transform: rotateY(0deg);
  z-index: 2;
  pointer-events: auto;
}

.flip-card-front {
  transform: rotateY(-180deg);
  z-index: 1;
  pointer-events: none;
}

.flip-card.revealed .flip-card-back {
  transform: rotateY(180deg);
  z-index: 1;
  pointer-events: none;
}

.flip-card.revealed .flip-card-front {
  transform: rotateY(0deg);
  z-index: 2;
  pointer-events: auto;
}

/* Staggered delays for multiple cards */
.flip-card.revealed .flip-card-back:nth-child(1) {
  transition-delay: 0s;
}

.flip-card.revealed .flip-card-back:nth-child(2) {
  transition-delay: 0.2s;
}

.flip-card.revealed .flip-card-back:nth-child(3) {
  transition-delay: 0.4s;
}

.flip-card.revealed .flip-card-front:nth-child(1) {
  transition-delay: 0s;
}

.flip-card.revealed .flip-card-front:nth-child(2) {
  transition-delay: 0.2s;
}

.flip-card.revealed .flip-card-front:nth-child(3) {
  transition-delay: 0.4s;
}

/* Pulse animation for mystery icon */
@keyframes pulse {
  0%, 100% {
    opacity: 0.7;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}
